<template>
  <v-container>
    <v-layout
      text-xs-center
      wrap
    >
    <qrcode-stream @decode="onDecode"></qrcode-stream>
    <p>{{msg}}</p>

    <!-- 读取提示弹窗 -->
    <div class="text-xs-center">
    <v-btn
      :disabled="dialog"
      :loading="dialog"
      class="white--text"
      color="purple darken-2"
      @click="dialog = true"
    >
      识别二维码
    </v-btn>
    <v-dialog
      v-model="dialog"
      hide-overlay
      persistent
      width="300"
    >
      <v-card
        color="primary"
        dark
      >
        <v-card-text>
          Please stand by
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    name: 'ScareCode',
    data () {
      return {
        msg: '',
        dialog: false
      }
    },
    watch: {
      dialog (val) {
        if (!val) return
        setTimeout(() => {
          this.dialog = false;
          this.$router.push({ path: '/Attendance' });
        }, 4000)
      }
    },
    methods: {
      onDecode (decodedString) {
        this.msg = decodedString;
      }
    }
  }
</script>

